ckground-color:#fff;outline:none}.buttonOnBrightBG:hover,.buttonOnBrightBG:focus,.buttonOnBrightBG:active{color:#5a0d67;background-color:#fff;outline:1px solid #5a0d67}.buttonDecor::before{content:"";position:absolute;width:80px;height:1px;background-color:#c4c4c4;right:-75px}.buttonDecor::after{content:"Жми";position:absolute;color:#c4c4c4;right:-110px}.socialLinks{width:125px;display:-webkit-box;display:-moz-box;display:-ms-flexbox;display:-webkit-flex;display:flex;font-size:0;align-items:center;flex-wrap:wrap}.socialLinks li{padding-right:10px}.socialLinks li:last-of-type{padding-right:0}